import {
	getUserCardInfo
} from '@/common/api/userInfo.js'
import {
	getTreatRecord
} from '@/common/api/outCall.js'
import {error} from '@/common/js/errorTips.js'
var _self = 1,
	timer = null;

export default {
	data() {
		return {
			treatRecordList: [],
			cardList: [],
			TabCur: 0,
			scrollLeft: 0,
			loadingText: '加载中...',
			page: 1,
		}
	},
	// 到达底部时会自动获取新数据
	onReachBottom: function() {
		let _self = this
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			if (_self.loadingText != '已加载全部') {
				_self.getTreatRecord()
			}
		}, 1000);
	},

	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			this.page = 1;
			this.getTreatRecord();
		},
		// 获取就诊卡列表
		getUserCardInfo: function() {
			uni.showLoading({
				title: '加载中'
			})
			this.cardList = []
			getUserCardInfo(uni.getStorageSync('accountID')).then(res => {
				if (res.data.code === 200) {
					this.cardList = res.data.data
					this.TabCur = this.cardList[0].id
					this.getTreatRecord()
				}
			}).catch(() => {
				uni.hideLoading()
				error('获取就诊卡列表失败')
			})
		},
		// 点击跳转到详情页面
		toDetail: function(ID) {
			uni.navigateTo({
				url: '/pagesA/pages/treatRecord/treatRecordDetail/' +
					'treatRecordDetail?recordID=' + ID
			})
		},
		// 获取用户就诊记录列表
		getTreatRecord: function() {
			uni.showLoading({
				title: '加载中'
			})
			let that = this;
			this.treatRecordList = [];
			getTreatRecord(this.TabCur, this.page, 2).then(res => {
				if (res.data.code === 200) {
					let data = res.data.data;
					if(data.list === null) {
						this.loadingText = '已加载全部'
					} else {
						if (that.page === 1) {
							that.treatRecordList = data.list
							that.page++
						} else {
							that.treatRecordList = that.treatRecordList.concat(data.list)
							that.page++
						} // end if
						
						if (data.list.length < 2) {
							this.loadingText = '已加载全部'
						} else {
							this.loadingText = '加载更多'
							// this.page++
						}
					}
					
					
				}
				uni.hideLoading()
			}).catch(() => {
				uni.hideLoading()
				error('获取就诊记录失败')
			})
		}
	},
	onLoad() {
		this.getUserCardInfo()
	}
}
